.Logo
{
    background-color: rgba(255, 255, 255, 0.863);
    padding: 8px;
    z-index: 10000;
    border-radius: 10px;
    box-sizing: border-box;
    height: 100%;
    margin-left: 160px;
}
.img
{
    height: 90%;
    transform: scale(1);
    transition: 0.3s linear;

}
.img:hover
{
    transform: scale(1.1);
}

@media (max-width:500px)
{
    .Logo
    {
        margin-left:0;
    }
}