.Toolbar
{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
     background: linear-gradient(rgb(233, 166, 41),rgb(163, 102, 32));
     display: flex;
     justify-content: space-between;   
     align-items: center;
     padding: 0 20px;
     box-sizing: border-box;
     z-index: 90;

}
.Toolbar nav
{
    height: 100%;
    
}

@media (max-width:499px)
{
    .OnlyDesktop
    {
        display: none;
    }
}