.kontrolki
{
    width: 100%;
    background: #CF8F2E;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}
.OrderButton
{
    background:linear-gradient(rgb(150, 75, 21), #f1bc87);
    width: 200px;
    margin-top: 20px;
    height: 40px;
    font-weight: 700;
    transition: linear 0.2s;
    transition: 0.2s linear;
    box-shadow: 0 0 4px black;
    color:  #1f0707;
}
.OrderButton:hover
{
    background:linear-gradient(#f1bc87,rgb(150, 75, 21));
    transform: scale(1.1);
}
.OrderButton:disabled
{
    background:linear-gradient(#707070,rgb(78, 78, 78));
    transform: scale(0.9);
    transition: 0.1s linear;
   
}
.OrderButton:disabled:hover{
    animation: disabl 0.3s linear ;
}


@keyframes disabl
{
 0%
 {
    transform: rotate(0deg); 
 }
 20%
 {
    transform: rotate(25deg); 
 }
 40%
 {
    transform: rotate(-25deg); 
 }
 60%
 {
    transform: rotate(10deg); 
 }
 80%
 {
    transform: rotate(-10deg); 
 }
 100%
 {
    transform: rotate(0deg);
 }
}
