.OrderButton
{
    background:-webkit-linear-gradient(rgb(150, 75, 21), #f1bc87);
    background:linear-gradient(rgb(150, 75, 21), #f1bc87);
    width: 200px;
    margin-top: 20px;
    height: 40px;
    font-weight: 700;
    -webkit-transition: linear 0.2s;
    transition: linear 0.2s;
    -webkit-transition: 0.2s linear;
    transition: 0.2s linear;
    box-shadow: 0 0 4px black;
    color:  #1f0707;
}
.OrderButton:hover
{
    background:-webkit-linear-gradient(#f1bc87,rgb(150, 75, 21));
    background:linear-gradient(#f1bc87,rgb(150, 75, 21));
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.OrderButton:disabled
{
    background:-webkit-linear-gradient(#707070,rgb(78, 78, 78));
    background:linear-gradient(#707070,rgb(78, 78, 78));
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: 0.1s linear;
    transition: 0.1s linear;
   
}
.OrderButton:disabled:hover{
    -webkit-animation: disabl 0.3s linear ;
            animation: disabl 0.3s linear ;
}


@-webkit-keyframes disabl
{
 0%
 {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); 
 }
 20%
 {
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg); 
 }
 40%
 {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg); 
 }
 60%
 {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg); 
 }
 80%
 {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); 
 }
 100%
 {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
 }
}


@keyframes disabl
{
 0%
 {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); 
 }
 20%
 {
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg); 
 }
 40%
 {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg); 
 }
 60%
 {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg); 
 }
 80%
 {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); 
 }
 100%
 {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
 }
}

