.Button {
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 0px;
    margin: 10px;
    font-size: 20px;
    font-weight: 700;
    transition:  all 0.2s linear;
}

.Success {
    color: rgb(0, 139, 23);
}

.Danger {
    color: #ff0037;
}

.divEl
{
    width: 50%;
    display: flex;
    height: 20%;
}
.Success:hover
{
    color: rgb(2, 255, 44);
}
.Danger:hover
{
    color: #6d0101;
}
p
{
    font-size: 17px;
}