.Nav
{
    padding: 0;
    margin: 0;
    list-style: none ;
    display: flex;
    align-items: center;
    height: 100%;
}

.NavItem
{
    margin: 0;
    box-sizing: border-box;
    display: flex;
    flex-flow: row;
    height: 100%;
    align-items: center;     

}
.NavItem a
{
    color: rgb(255, 255, 255);
    text-decoration: none;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
    box-sizing: border-box;
    transition: 0.3s linear; 
    display: block;   
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active
{
background-color: rgb(243, 176, 113);
border-bottom: 4px solid rgb(2, 75, 158); 
color: black;
}





@media (max-width:500px)
{
    .Nav
    {
        padding: 0;
        margin: 0;
        list-style: none ;
        display: flex;
        flex-flow:column;
        align-items: center;
        height: 100%;
        width: auto;
    }
    

.NavItem
{
    margin:10px 0;
    display: flex;
    width: 100%;
    align-items: center;     

}
.NavItem a
{
    color: rgb(243, 158, 124);
    width:100%;
    border-bottom: 4px solid blue;
    display: block;   
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active
{
background-color: rgb(255, 255, 255);
}

}
