.Modal {
    position: fixed;
    z-index: 500;
    background:linear-gradient(rgb(238, 195, 101),rgb(238, 168, 163));
    width: 70%;
    font-size: 20px;
    border: 3px solid rgb(0, 0, 0);
    box-shadow: 1px 1px 3px rgb(2, 2, 2);
    padding: 16px;
    left: 15%;
    border-radius: 20px;
    transform: translateY(0vw);
    top: 30%;
    transform: scale(1);
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}
.Modal.active
{ 
    transform: scale(0);
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}